.range {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    line-height: 15px;
    border: 1px solid black;
    background: white;
    transition: background 0.5s ease;
    color: black;
    border-radius: 8px;
    box-sizing: border-box;
    cursor: pointer;
    &_dark {
      border: 1px solid white;
      background: #000;
      color: white;
    }
    &_open {
      border-bottom: 1px solid transparent;
      border-radius: 8px 8px 0 0;
    }
    &__title {
      margin: 14px 0 15px 14px;
    }
    &__arrow {
      position: absolute;
      display: flex;
      right: 18px;
      float: right;
      fill: #b3b3b3;
    }
    &__сontainer {
      display: flex;
      position: absolute;
      justify-content: center;
      align-items: center;
      align-self: center;
      width: calc(100% + 2px);
      top: 43px;
      border: 1px solid black;
      padding: 20px 19px;
      box-sizing: border-box;
      border-radius: 0 0 8px 8px;
      background: white;
      z-index: 1;
      &_dark {
        border: 1px solid white;
        background: #000;
        color: white;
      }
      &_open {
        border-top: none;
      }
    }
  }